.heyjack-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: linear-gradient(90deg, #16222A, #3A6073);
  padding: 20px;
  box-sizing: border-box;
}

.heyjack-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 0 20px;
}

.heyjack-title {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 800;
  margin: 0;
  text-shadow: 0 4px 12px rgba(0,0,0,0.4), 0 2px 6px rgba(0,0,0,0.3), 0 0 20px rgba(255,255,255,0.1);
  letter-spacing: 2px;
}

.heyjack-error {
  background-color: rgba(255, 0, 0, 0.1);
  border: 1px solid rgba(255, 0, 0, 0.3);
  border-radius: 8px;
  padding: 15px;
  margin: 0 20px 20px 20px;
  color: #ffcccc;
}

.heyjack-error p {
  margin: 0 0 8px 0;
  font-weight: bold;
}

.heyjack-error small {
  display: block;
  font-size: 0.85rem;
  opacity: 0.8;
}

.heyjack-chat {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  overflow: hidden;
  margin: 0 20px 20px 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.heyjack-messages {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.heyjack-empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
  color: #999;
  text-align: center;
  padding: 40px 20px;
}

.heyjack-empty-state p {
  font-size: 1.2rem;
  margin-bottom: 8px;
  color: #666;
}

.heyjack-empty-state small {
  font-size: 0.9rem;
  color: #999;
}

.heyjack-message {
  display: flex;
  max-width: 75%;
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.heyjack-message-user {
  align-self: flex-end;
}

.heyjack-message-assistant {
  align-self: flex-start;
}

.heyjack-message-content {
  padding: 12px 18px;
  border-radius: 18px;
  word-wrap: break-word;
  line-height: 1.5;
  font-size: 1rem;
}

.heyjack-message-user .heyjack-message-content {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-bottom-right-radius: 4px;
}

.heyjack-message-assistant .heyjack-message-content {
  background: #e8e8e8;
  color: #333;
  border-bottom-left-radius: 4px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

/* Markdown styling for assistant messages */
.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-p {
  margin: 0 0 12px 0;
  line-height: 1.6;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-p:last-child {
  margin-bottom: 0;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-ul,
.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-ol {
  margin: 8px 0 12px 0;
  padding-left: 24px;
  line-height: 1.8;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-li {
  margin: 4px 0;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-ul .heyjack-markdown-li {
  list-style-type: disc;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-ol .heyjack-markdown-li {
  list-style-type: decimal;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-h1,
.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-h2,
.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-h3 {
  margin: 16px 0 8px 0;
  font-weight: 700;
  line-height: 1.4;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-h1 {
  font-size: 1.5rem;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-h2 {
  font-size: 1.3rem;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-h3 {
  font-size: 1.1rem;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-strong {
  font-weight: 700;
  color: #1a1a1a;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-code-inline {
  background: rgba(0, 0, 0, 0.1);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
  font-size: 0.9em;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-code-block {
  display: block;
  background: rgba(0, 0, 0, 0.1);
  padding: 12px;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-size: 0.9em;
  overflow-x: auto;
  margin: 12px 0;
  max-width: 100%;
}

/* Improve readability for long responses */
.heyjack-message-assistant .heyjack-message-content {
  max-width: 100%;
  overflow-wrap: break-word;
  word-break: break-word;
}

/* Better spacing for lists within paragraphs */
.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-p + .heyjack-markdown-ul,
.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-p + .heyjack-markdown-ol {
  margin-top: 8px;
}

/* Link styling */
.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-a {
  color: #3182ce;
  text-decoration: underline;
  transition: color 0.2s;
}

.heyjack-message-assistant .heyjack-message-content .heyjack-markdown-a:hover {
  color: #2c5aa0;
}

.heyjack-typing-indicator {
  display: flex;
  gap: 5px;
  padding: 8px 0;
}

.heyjack-typing-indicator span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #666;
  animation: typing 1.4s infinite;
}

.heyjack-typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.heyjack-typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%, 60%, 100% {
    transform: translateY(0);
    opacity: 0.7;
  }
  30% {
    transform: translateY(-10px);
    opacity: 1;
  }
}

.heyjack-input-form {
  display: flex;
  padding: 15px 20px;
  background: white;
  border-top: 1px solid #e0e0e0;
  gap: 10px;
}

.heyjack-input {
  flex: 1;
  padding: 12px 18px;
  border: 2px solid #e0e0e0;
  border-radius: 24px;
  font-size: 1rem;
  outline: none;
  transition: border-color 0.3s;
}

.heyjack-input:focus {
  border-color: #667eea;
}

.heyjack-input:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

.heyjack-send-button {
  padding: 12px 30px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 24px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}

.heyjack-send-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.heyjack-send-button:active:not(:disabled) {
  transform: translateY(0);
}

.heyjack-send-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Scrollbar styling */
.heyjack-messages::-webkit-scrollbar {
  width: 8px;
}

.heyjack-messages::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.heyjack-messages::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.heyjack-messages::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Responsive design */
@media (max-width: 768px) {
  .heyjack-container {
    padding: 10px;
  }

  .heyjack-header {
    flex-direction: column;
    gap: 15px;
    padding: 0;
  }

  .heyjack-title {
    font-size: 2rem;
  }

  .heyjack-chat {
    margin: 0 10px 10px 10px;
  }

  .heyjack-message {
    max-width: 85%;
  }

  .heyjack-error {
    margin: 0 10px 10px 10px;
  }
}

